.title {
  font-family: $mossport;
  font-weight: 700;
  font-size: 10rem;
  line-height: 1.01;
  color: $red;
  text-transform: uppercase;
  text-align: center;
  margin: 0 0 6rem;
  letter-spacing: 0.01rem;

  &--white {
    color: $white;
  }

  @media (max-width: $tablet) {
    font-size: 6rem;
    line-height: 1;
  }

  @media (max-width: $mobile) {
    font-size: 4.4rem;
    line-height: 1;
  }
}
